'use client'

import { usePathname, useRouter } from 'next/navigation'
import { Button } from '@/components/ui/button'
import { locales, type Locale } from '@/i18n/config'

interface LanguageSwitcherProps {
  currentLocale?: Locale
}

export function LanguageSwitcher({ currentLocale = 'sr' }: LanguageSwitcherProps) {
  const router = useRouter()
  const pathname = usePathname()
  
  const handleLanguageChange = (newLocale: Locale) => {
    // Get current path without locale
    const segments = pathname.split('/').filter(Boolean)
    
    // Remove current locale if present
    if (locales.includes(segments[0] as Locale)) {
      segments.shift()
    }
    
    // Build new path with new locale
    const newPath = `/${newLocale}${segments.length > 0 ? '/' + segments.join('/') : ''}`
    
    router.push(newPath)
  }
  
  const getLanguageCode = (locale: Locale) => {
    switch(locale) {
      case 'en': return 'EN'
      case 'sr': return 'SR'
      case 'sr-cyrl': return 'СР'
      default: return locale.toUpperCase()
    }
  }
  
  return (
    <div className="flex items-center gap-3">
      {locales.map((locale) => (
        <Button
          key={locale}
          variant="ghost"
          size="sm"
          onClick={() => handleLanguageChange(locale)}
          className={`px-3 ${currentLocale === locale ? 'font-bold' : 'font-normal'}`}
        >
          {getLanguageCode(locale)}
        </Button>
      ))}
    </div>
  )
}